<template>
  <div>
    <Spinner :class="{ 'fade-out': !isLoading }" />
    <h1 @click.prevent.stop="clk">類別管理</h1>
    <BackStageNav />
    <table class="table">
      <thead class="thead-dark">
        <tr>
          <th class="text-center" scope="col">#</th>
          <th scope="col">Name</th>
          <th scope="col">Email</th>
          <th scope="col">身分組設定</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="user in users" :key="user.id">
          <td scope="row">{{ user.id }}</td>
          <td>{{ user.name }}</td>
          <td>
            {{ user.email }}
          </td>
          <td>
            <form class="input-group">
              <select class="form-control" v-model="user.role" disabled>
                <option :selected="user.role === 'user'">user</option>
                <option :selected="user.role === 'admin'">admin</option>
              </select>
            </form>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import BackStageNav from '../components/backstage/BackStageNav'
import usersAPI from '../apis/users'
import Spinner from '../components/Spinner'
import { iziToast } from '../utils/helpers'
export default {
  name: 'AdminUsers',
  components: {
    BackStageNav,
    Spinner,
  },
  data() {
    return {
      users: [],
      isLoading: true,
    }
  },
  methods: {
    async fetchData() {
      try {
        this.isLoading = true
        const { data } = await usersAPI.getUsers()
        this.users = data.users
        this.isLoading = false
      } catch (err) {
        console.log(err)
        this.$toast.error(' ', '操作失敗，請稍後再試', iziToast.options.error)
        this.isLoading = false
      }
    },
    clk() {
      this.$toast.show(' ', '登入成功', iziToast.options.success)
    },
  },
  created() {
    this.fetchData()
  },
}
</script>
